  /*
    From
    To
    EnPas
    Promoted Sq
    Pawn Start
    Castel Move
  */
  /*
    Siigned Int
    0000 0000 0000 0000 0000 0111 1111 -> From 0x7F
    0000 0000 0000 0011 1111 1000 0000 -> To >> 7, 0x7F
    0000 0000 0011 1100 0000 0000 0000 -> Captured >> 14, 0xF
    0000 0000 0100 0000 0000 0000 0000 -> EP 0x40000
    0000 0000 1000 0000 0000 0000 0000 -> Pawn Start 0x80000
    0000 1111 0000 0000 0000 0000 0000 -> Promoted Piece >> 20, 0xF
    0001 0000 0000 0000 0000 0000 0000 -> Castle 0x1000000
  */
  // bit wise operation to gen all info for move gen
  FROMSQ(m) { return (m & 0x7F); }
  TOSQ(m)  { return (((m)>>7) & 0x7F); }
  CAPTURED(m)  { return (((m)>>14) & 0xF); }
  PROMOTED(m)  { return (((m)>>20) & 0xF); }
  // enPas flag
  MFLAGEP = 0x40000
  // pceStart flag
  MFLAGPS = 0x80000
  // Castel flag
  MFLAGCA = 0x100000
  // Capture flag 
  MFLAGCAP = 0x7C000
  // Promote flag
  MFLAGPROM = 0xF00000
  
  NOMOVE = 0
  // gen of random number of 32 bits
  RAND_32() {
    return (Math.floor((Math.random()*255)+1) << 23) | (Math.floor((Math.random()*255)+1) << 16)
       | (Math.floor((Math.random()*255)+1) << 8) | Math.floor((Math.random()*255)+1);
  
  }
  // macros 
  PCEINDEX(pce, pceNum) {
    return (pce * 10 + pceNum);
  }
  
  FR2SQ(f,r) {
     return ( (21 + (f) ) + ( (r) * 10 ) );
  }
  
  SQ64(sq120) { 
    return this.Sq120ToSq64[(sq120)];
  }
  
  SQ120(sq64) {
    return this.Sq64ToSq120[(sq64)];
  }
  SQOFFBOARD(sq){
    if(this.FilesBrd[sq] == this.squares.OFFBOARD){
      return this.BOOL.TRUE
    }
    else{
      return this.BOOL.FALSE
    }
  }

  // hash keys
  PieceKeys = new Array(14 * 120)
  SideKey = 0
  CastleKeys = new Array(16)

  
